.video {
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  color: @font-regular;
  box-sizing: border-box;
  margin: 0 auto;

  @media (max-width: @mobile-width-only) {
    width: 320px;
    padding: 0 30px;
  }

  @media (min-width: @tablet-width) {
    width: 768px;
    padding: 0 50px;
  }

  @media (min-width: @desktop-width) {
    width: 1150px;
  }
}

.video__wrapper {
  margin-bottom: 58px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  border: 1px solid @thin-grey-line;

  @media (min-width: @tablet-width) {
    margin-bottom: 65px;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(6, 175px);
    grid-template-rows: auto;
    grid-template-areas: "photo photo photo photo title title" "photo photo photo photo desc desc" "photo photo photo photo button button" "photo photo photo photo . .";
    padding-bottom: 0;
    margin-bottom: 110px;

    &:hover {
      box-shadow: 0 6px 10px 0 @thin-grey-line;
    }

    &:hover:active {
      box-shadow: 0 3px 5px 0 @thin-grey-line;
    }
  }
}

.video__title {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  position: relative;
  margin: 0;
  padding: 20px 34px;
  display: flex;
  align-items: center;
  letter-spacing: 1px;

  @media (min-width: @tablet-width) {
    order: 2;
    font-size: 20px;
    line-height: 24px;
    margin-top: 20px;
    padding-bottom: 24px;
  }

  @media (min-width: @desktop-width) {
    width: 180px;
    margin-top: 30px;
    padding-left: 60px;
    grid-area: title;
    flex-direction: column;
    align-self: flex-end;
    align-items: flex-start;
  }
}

.video__title::before {
  position: relative;
  margin-right: 25px;
  content: url("../img/icon-camera.svg");
  top: 5px;

  @media (min-width: @tablet-width) {
    left: 7px;
    margin-right: 40px;
  }

  @media (min-width: @desktop-width) {
    top: 1px;
    left: 1px;
    margin: 0;
    margin-bottom: 27px;
    align-self: flex-start;
  }
}

.video__play {
  position: relative;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  justify-self: center;

  @media (max-width: @mobile-width-only) {
    background-image: url("../img/video-mobile@1x.jpg");
    width: 260px;
    height: 150px;
    padding-top: 13px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/video-mobile@2x.jpg");
    }
  }

  @media (min-width: @tablet-width) {
    background-image: url("../img/video-tablet@1x.jpg");
    width: 668px;
    height: 455px;
    order: 1;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/video-tablet@2x.jpg");
    }
  }

  @media (min-width: @desktop-width) {
    background-image: url("../img/video-desktop@1x.jpg");
    width: 682px;
    height: 455px;
    grid-area: photo;
    justify-content: stretch;
    justify-self: flex-start;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/video-desktop@2x.jpg");
    }
  }
}

.video__description {
  font-weight: 400;
  font-size: 13px;
  line-height: 24px;
  padding: 28px 33px 41px;
  margin: 0;
  color: @darken-blue-black;

  @media (min-width: @tablet-width) {
    order: 3;
    font-size: 15px;
    line-height: 30px;
    padding: 0 45px 40px 41px;
    word-spacing: 3px;
  }

  @media (min-width: @desktop-width) {
    grid-area: desc;
    width: 240px;
    padding-bottom: 0;
    padding-left: 60px;
    margin: 0;
  }
}

.video__play-button {
  position: absolute;
  border: none;
  background: transparent;
  width: 100px;
  height: 77px;
  top: 43px;
  left: 73px;

  @media (min-width: @tablet-width) {
    top: 189px;
    left: 277px;
  }

  @media (min-width: @desktop-width) {
    grid-area: button;
    top: 195px;
    left: 285px;

    &:hover {
      svg {
        transform: scale(1.2);
      }
    }

    &:hover:active {
      svg {
        transform: scale(0.8);
        opacity: 0.3;
      }
    }
  }
}

.video__button {
  text-decoration: none;
  text-align: center;
  color: @darken-blue-black;
  padding: 15px 13px 16px;
  margin: 0 34px 29px;
  border: 2px solid @darken-blue-black;
  background: transparent;

  @media (min-width: @tablet-width) {
    order: 4;
    padding: 15px 13px 16px;
    margin: 0 42px 42px;
  }

  @media (min-width: @desktop-width) {
    grid-column: 5 / 7;
    margin-left: 60px;
    margin-right: 57px;
    margin-top: 50px;
  }

  &:hover {
    background-color: @darken-blue-black;
    color: @font-white;
  }

  &:hover:active {
    background-color: @darken-blue-black;
    color: @font-white;

    .video__button-text {
      opacity: 0.3;
    }
  }
}

.video__button-text {
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
}
